import React, {Component} from 'react';
import { buildUrl
    , SIGNAL_TYPE_LIST
    , SIGNAL_BY_DATE_TYPE 
    , PRICE_COMPOSITE_BY_ID
    , SIGNAL_BY_DATE_TYPES
    , stockChartsUrl } from '../../config/UrlConfig';
import axios from 'axios';

class Signals extends Component {
    constructor() {
        super();
        this.state = {
            signalTypeList: [
                {
                    signalCode:'4WKUP',
                    signalDesc: 'Loading'
                }
            ],
            signalList: [
                {
                    signalId: 'INIT',
                    signalType:'GAPUP',
                    priceDate: '2018-06-02',
                    tickerSymbol: 'Loading'
                }
            ],
            selectedSignal: '',
            selectedSignalDesc: ''
        }
    }
    
    retrieveSignalTypes = ()=> {
        axios.get(buildUrl(SIGNAL_TYPE_LIST))
        .then(
            res=>{
                this.setState({
                    signalTypeList: res.data,
                    selectedSignal: res.data[0].signalCode,
                    selectedSignalDesc: res.data[0].signalDesc,

                });
                this.retrieveSignals(res.data[0].signalCode);
            },
            res=>{
                console.log("retrieveSignalTypes failed", res);
            }
        );
    }

    findSignalDesc = (signalCode)=>{
        this.state.signalTypeList.forEach((st)=>{
            if (st.signalCode === signalCode) {
                this.setState({selectedSignalDesc: st.signalDesc});
            }
        })
    }

    retrieveSignals = (signalType, signalDate)=>{
        console.log('retrieveSignals', signalDate);
        let queryDate = signalDate;
        if (queryDate === undefined) {
            queryDate = this.state.selectedSignalDate;
        }
        let request = {
            signalDate: queryDate,
            signalType: signalType
        }
        axios.post(buildUrl(SIGNAL_BY_DATE_TYPE), request)
        .then(
            res=>{
                this.setState(
                    {
                        signalList: res.data,
                    }
                )
                this.findSignalDesc(signalType);
                if (res.data.length > 0) {
                    this.setState({
                        selectedSignalDate: res.data[0].priceDate.substring(0,10),
                        selectedSignal: signalType
                    })
                }
            },
            res=>{
                console.log("retrieveSignals failed", res.data);
            }
        );
    }

    retrieveSignalsWithOverlay = (overlaySignal)=> {
        let request = {
            signalDate: this.state.selectedSignalDate,
            signalType: this.state.selectedSignal,
            overlaySignalType: overlaySignal
        }
        axios.post(buildUrl(SIGNAL_BY_DATE_TYPES), request)
        .then(
            res=>{
                this.setState(
                    {
                        signalList: res.data
                    }
                );
            },
            res=>{
                console.log('retrieveSignalsWithOverlay failed', res.data);
            }
        );
    }

    retrieveCompositePrice = (priceId)=> {

    }

    noResults = ()=>{
        let nrTag = ''
        if (this.state.signalList.length === 0) {
            nrTag = <div>No results found</div>
        }
        return nrTag;
    }

    handleDateChg = (e)=>{
        console.log('handleDateChg', e.target.value);
        this.setState({
            selectedDate: e.target.value
        });
        this.retrieveSignals(this.state.selectedSignal, e.target.value);
    }

    handleOverlaySelect = (e)=>{
        console.log('handleOverlaySelect', e.target.value);
        this.retrieveSignalsWithOverlay(e.target.value);
    }

    componentDidMount() {
        this.retrieveSignalTypes();
    }

    render() {
        return (
            <div>
                <div className='content-grid'>
                    <div>
                        <div>Signals</div>
                        <div>
                            <ul>
                            {
                            this.state.signalTypeList.map(st=>
                                <li className="sub-title" key={st.signalCode}>
                                    <a href='#' onClick={()=>this.retrieveSignals(st.signalCode)}>{st.signalDesc}</a>
                                </li>
                            )
                            }
                            </ul>
                        </div>
                    </div>
                    <div>
                        <div>
                            <label htmlFor="priceDate" className="sub-title">Price Date</label>
                            <input type="date" id="priceDate" 
                                defaultValue={this.state.selectedSignalDate} 
                                onChange={this.handleDateChg}/>
                        </div>
                        <div>
                            <span style={{fontWeight:'bold', padding:'2px 15px 2px 2px'}}>{this.state.selectedSignalDesc}</span>
                            <span>
                                <select onChange={this.handleOverlaySelect}>
                                    {this.state.signalTypeList.map(t=>
                                        <option key={t.signalCode} value={t.signalCode} selected={t.signalCode===this.state.selectedSignal}>{t.signalDesc}</option>
                                    )}
                                </select>
                            </span>
                        </div>
                        <div className="info-grid">
                        {this.state.signalList.map(s=>
                            <div key={s.signalId}>
                                <div className={s.multiList?'multi-list':'single-list'}><a href={stockChartsUrl(s.tickerSymbol)} target='_blank'>{s.tickerSymbol}</a></div>
                                <div className="sub-title">Closing Price</div>
                                <div>{Number(s.closePrice).toFixed(2)}</div>
                                <div className="sub-title">Volume</div>
                                <div>{s.volume}</div>
                            </div>
                        )}
                        {
                            this.noResults()
                        }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Signals;


